body {
    background-color:rgb(246, 240, 240);
    box-sizing: border-box;
}

.aumentafonte {
    font-size: 2rem;
}

.calendar {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.calbox {
    display: flex;
    flex-wrap: wrap;
    max-width: 1200px;
}

.calboxson {
    flex:1;
    border: 3px solid black;
    background-color: wheat;
    min-width: 120px;
    min-height: 200px;
    text-align: center;
}
.homemain {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
}

.info {
    text-align: justify;
    font-size: 2rem;
    max-width: 60%;
}

.fcontainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-left: 5%;
    margin-right: 5%;
}

.fitem { 
    color: black
}
